	background-color: #ba271f;
	transition: 0.5s
}

header .header-top .nav ul li a.active:after {
	content: " ";
	position: absolute;
	bottom: -8px;
	left: 14px;
	width: 100px;
	height: 1px;
	background-color: #fff;
	pointer-events: none
}

header .header-title {
	text-align: right
}

